.main-timeline3 {
  overflow: hidden;
  position: relative
}

.main-timeline3 .timeline {
  position: relative;
  margin-top: -79px
}

.main-timeline3 .timeline:first-child {
  margin-top: 0
}

.main-timeline3 .timeline-icon,
.main-timeline3 .year {
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0
}

.main-timeline3 .timeline:after,
.main-timeline3 .timeline:before {
  content: "";
  display: block;
  width: 100%;
  clear: both
}

.main-timeline3 .timeline:before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2
}

.main-timeline3 .timeline-icon {
  width: 210px;
  height: 210px;
  border-radius: 50%;
  border: 25px solid transparent;
  border-top-color: #f76eff;
  border-right-color: #f76eff;
  z-index: 1;
  transform: rotate(45deg)
}

.main-timeline3 .year {
  display: flex;
  width: 110px;
  height: 110px;
  border-radius: 50%;
  background: #fff;
  box-shadow: 0 0 20px rgba(0, 0, 0, .4);
  font-size: 20px;
  font-weight: 700;
  color: #f76eff;
  text-align: center;
  transform: rotate(-45deg);
  align-items: center;
  justify-content: center;
}

.main-timeline3 .timeline-content {
  width: 35%;
  float: right;
  background: #f76eff;
  color: #48ffb4;
  padding: 30px 20px;
  margin: 50px 0;
  box-shadow: 0 10px 25px -10px rgba(72, 29, 99, .3);
  z-index: 1;
  position: relative
}

.main-timeline3 .timeline-content:before {
  content: "";
  width: 20%;
  height: 15px;
  background: #f76eff;
  position: absolute;
  top: 50%;
  left: -20%;
  z-index: -1;
  transform: translateY(-50%)
}

.main-timeline3 .title {
  font-size: 20px;
  font-weight: 700;
  color: #48ffb4;
  margin: 0 0 10px
}

.main-timeline3 .description {
  font-size: 16px;
  line-height: 24px;
  color: #48ffb4;
  margin: 0
}

.main-timeline3 .timeline:nth-child(2n) .timeline-icon {
  transform: rotate(-135deg);
  border-top-color: #60fff1;
  border-right-color: #60fff1
}

.main-timeline3 .timeline:nth-child(2n) .year {
  transform: rotate(135deg);
  color: #60fff1
}

.main-timeline3 .timeline:nth-child(2n) .timeline-content {
  float: left
}

.main-timeline3 .timeline:nth-child(2n) .timeline-content:before {
  left: auto;
  right: -20%
}

.main-timeline3 .timeline:nth-child(2n) .timeline-content,
.main-timeline3 .timeline:nth-child(2n) .timeline-content:before {
  background: #60fff1;
}


.main-timeline3 .timeline:nth-child(2n) .timeline-content .title {
  color: #ffb0f5;
}

.main-timeline3 .timeline:nth-child(2n) .timeline-content .description {
  color: #ffb0f5;
}

.main-timeline3 .timeline:nth-child(3n) .timeline-icon {
  border-top-color: #ffb0f5;
  border-right-color: #ffb0f5
}

.main-timeline3 .timeline:nth-child(3n) .year {
  color: #ffb0f5
}

.main-timeline3 .timeline:nth-child(3n) .timeline-content,
.main-timeline3 .timeline:nth-child(3n) .timeline-content:before {
  background: #ffb0f5;
}

.main-timeline3 .timeline:nth-child(3n) .timeline-content .title {
  color: #60fff1;
}

.main-timeline3 .timeline:nth-child(3n) .timeline-content .description {
  color: #60fff1;
}

.main-timeline3 .timeline:nth-child(4n) .timeline-icon {
  border-top-color: #48ffb4;
  border-right-color: #48ffb4
}

.main-timeline3 .timeline:nth-child(4n) .year {
  color: #48ffb4
}

.main-timeline3 .timeline:nth-child(4n) .timeline-content,
.main-timeline3 .timeline:nth-child(4n) .timeline-content:before {
  background: #48ffb4;
}
.main-timeline3 .timeline:nth-child(4n) .timeline-content .title {
  color: #f76eff;
}

.main-timeline3 .timeline:nth-child(4n) .timeline-content .description {
  color: #f76eff;
}

@media only screen and (max-width:1199px) {
  .main-timeline3 .timeline {
    margin-top: -103px
  }
  .main-timeline3 .timeline-content:before {
    left: -18%
  }
  .main-timeline3 .timeline:nth-child(2n) .timeline-content:before {
    right: -18%
  }
}

@media only screen and (max-width:990px) {
  .main-timeline3 .timeline {
    margin-top: -127px
  }
  .main-timeline3 .timeline-content:before {
    left: -2%
  }
  .main-timeline3 .timeline:nth-child(2n) .timeline-content:before {
    right: -2%
  }
}

@media only screen and (max-width:767px) {
  .main-timeline3 .timeline {
    margin-top: 0;
    overflow: hidden
  }
  .main-timeline3 .timeline:before,
  .main-timeline3 .timeline:nth-child(2n):before {
    box-shadow: none
  }
  .main-timeline3 .timeline-icon,
  .main-timeline3 .timeline:nth-child(2n) .timeline-icon {
    margin-top: -30px;
    margin-bottom: 20px;
    position: relative;
    transform: rotate(135deg)
  }
  .main-timeline3 .timeline:nth-child(2n) .year,
  .main-timeline3 .year {
    transform: rotate(-135deg)
  }
  .main-timeline3 .timeline-content,
  .main-timeline3 .timeline:nth-child(2n) .timeline-content {
    width: 100%;
    float: none;
    border-radius: 0 0 20px 20px;
    text-align: center;
    padding: 25px 20px;
    margin: 0 auto
  }
  .main-timeline3 .timeline-content:before,
  .main-timeline3 .timeline:nth-child(2n) .timeline-content:before {
    width: 15px;
    height: 25px;
    position: absolute;
    top: -22px;
    left: 50%;
    z-index: -1;
    transform: translate(-50%, 0)
  }
}