.main-timeline3 {
  overflow: hidden;
  position: relative;
}

.main-timeline3 .timeline {
  position: relative;
  margin-top: -79px;
}

.main-timeline3 .timeline:first-child {
  margin-top: 0;
}

.main-timeline3 .timeline-icon,
.main-timeline3 .year {
  margin: auto;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

.main-timeline3 .timeline:after,
.main-timeline3 .timeline:before {
  content: "";
  display: block;
  width: 100%;
  clear: both;
}

.main-timeline3 .timeline:before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
}

.main-timeline3 .timeline-icon {
  width: 210px;
  height: 210px;
  border-radius: 50%;
  border: 25px solid transparent;
  border-top-color: #f76eff;
  border-right-color: #f76eff;
  z-index: 1;
  transform: rotate(45deg);
}

.main-timeline3 .year {
  display: flex;
  width: 110px;
  height: 110px;
  border-radius: 50%;
  background: #fff;
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.4);
  font-size: 20px;
  font-weight: 700;
  color: #f76eff;
  text-align: center;
  transform: rotate(-45deg);
  align-items: center;
  justify-content: center;
}

.main-timeline3 .timeline-content {
  width: 35%;
  float: right;
  background: #f76eff;
  color: #48ffb4;
  padding: 30px 20px;
  margin: 50px 0;
  box-shadow: 0 10px 25px -10px rgba(72, 29, 99, 0.3);
  z-index: 1;
  position: relative;
}

.main-timeline3 .timeline-content:before {
  content: "";
  width: 20%;
  height: 15px;
  background: #f76eff;
  position: absolute;
  top: 50%;
  left: -20%;
  z-index: -1;
  transform: translateY(-50%);
}

.main-timeline3 .title {
  font-size: 20px;
  font-weight: 700;
  color: #48ffb4;
